<template>
  
  <div class="flag-holder grid grid-flow-col">
      <!-- <select v-model="$i18n.locale">
        <option v-for="(locale, i) in locales" :key="`locale-${i}`" :value="locale">
            <flag
            :iso="locale"
            v-bind:squared="false"
            style="font-size: 25px; margin-right: 5px"
            />
        </option>
    </select> -->
    <button value="pt" @click="changeLang('pt')">
        <flag
        iso="pt"
        :squared="false"
        class="flag"
        />
    </button>
    <button value="en" @click="changeLang('en')">
        <flag
        iso="gb"
        :squared="false"
        class="flag"
        />
    </button>
  </div>
</template>

<script>
export default {
  name: "LocaleSwitcher",
  data() {
    return { locales: {pt: "pt", en: "gb"} };
  },
  methods: {
      changeLang(val) {
          this.$i18n.locale = val
      }
  }
};
</script>

<style scoped>
.flag-holder{
    float: right;
    margin-top: 5px;
    /* padding-left: 100rem; */
}
.flag {
    font-size: 27px;
    margin-left: 10px;
    border-radius: 5px;
}
</style>